// Backgrouns, Colors and typografy.
$f-family:             'Trebuchet MS' 
$bg-color:           #191919
$bg-color-float-top:  #181818
$bg-btn-reset:          #d55c5a
$bg-btn-click:          #32CD32 
$color-black:            #000
$color-white:            #fff  
$color-shadow-1-and-3:#ffffff0b
$color-shadow-2:        #ffdd0333
$color-shadow-btn:       #ffffff27

// Styles Generals
.App
    text-align: center
    width: 100%
    height: 100vh
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    background-color: $bg-color
    position: relative
    .contador-float-top
        width: 100px
        height: 100px
        border-radius: 50%
        background-color: $bg-color-float-top
        box-shadow: 0 0 20px $color-shadow-1-and-3,0 0 20px $color-shadow-2,0 0 30px $color-shadow-1-and-3
        position: absolute
        left: 20px
        top: 20px
        p
            font-size: 50px
            line-height: 100px
    h1
        color: white
        font-family: $f-family
        font-size: 100px
        letter-spacing: 1px
    p
        font-family: $f-family
        font-weight: bold
        font-size: 120px
        color: $color-white
    button
        margin-top: 40px
        width: 25rem
        height: 3.2rem
        background-color: $color-black
        border: none
        box-shadow: 0 0 10px 2px $color-shadow-btn
        color: white
        font-family: $f-family
        font-size: 18px
        cursor: pointer
    //.btn
    .btn-reset
        background-color: $bg-btn-reset
    .btn_click
        background-color: $bg-btn-click        
.succes
    color: $bg-btn-click   !important


@media screen and (min-width: 1440px)
    .App
        button
            width: 75rem
            height: 4rem
            font-size: 25px
            letter-spacing: 20px

@media screen and (max-width: 1024px)
        .App
            .contador-float-top
                width: 50px
                height: 50px
                p
                    font-size: 25px
                    line-height: 50px
            h1
                font-size: 60px
            p
                font-size: 70px  
            button
                margin-top: 30px

@media screen and (max-width: 425px)
    .App
        button
            width: 20rem
            height: 3.2rem 
            margin-top: 30px
@media screen and (max-width: 320px)
    .App
        button
            width: 15rem
            height: 3.2rem
